-shizuku-text-color: #333;\n  --shizuku-theme-color: #2bd;\n}\n\n#write {\n  font-family: 'Avenir Next', 'PingFang SC', 'Microsoft YaHei UI',\n    'Microsoft YaHei', sans-serif;\n  font-size: 16px;\n  word-wrap: break-word;\n  text-align: justify;\n}\n#write > *:first-child {\n  margin-top: 0 !important;\n}\n#write > *:last-child {\n  margin-bottom: 0 !important;\n}\n#write a,\n#write a:hover,\n#write a:active,\n#write a:focus,\n#write a:visited {\n  color: var(--shizuku-theme-color);\n  text-decoration: none;\n}\n#write a:not([href]) {\n  color: inherit;\n  text-decoration: none;\n}\n#write p,\n#write blockquote,\n#write ul,\n#write ol,\n#write dl,\n#write table,\n#write pre {\n  text-align: justify;\n  word-wrap: break-word;\n  word-break: break-all;\n}\n#write hr {\n  display: inline-block;\n  width: 25px;\n  height: 1px;\n  line-height: 1px;\n  border: 0;\n  margin: 0;\n  background: rgba(128, 128, 128, 0.4);\n  vertical-align: middle;\n}\n#write .md-hr {\n  outline: none !important;\n}\n#write .md-hr.md-focus hr {\n  background: var(--shizuku-theme-color);\n}\n#write blockquote {\n  position: relative;\n}\n#write blockquote > p {\n  margin-bottom: 10px;\n}\n#write .loweralpha {\n  list-style-type: lower-alpha;\n}\n#write .md-blockmeta,\n#write .md-header-span {\n  font-style: normal;\n  font-weight: normal;\n  color: rgba(128, 128, 128, 0.4);\n}\n#write .md-header-span {\n  background: rgba(128, 128, 128, 0.2);\n}\n#write h1,\n#write h2,\n#write h3,\n#write h4,\n#write h5,\n#write h6 {\n  font-weight: bold;\n  line-height: 1.25;\n}\n#write h1,\n#write h2,\n#write h3,\n#write h4,\n#write h5,\n#write h6 {\n  vertical-align: baseline;\n}\n#write h1::before,\n#write h2::before,\n#write h3::before,\n#write h4::before,\n#write h5::before,\n#write h6::before {\n  border: 0 none !important;\n  border-radius: 0 !important;\n  position: static !important;\n  left: auto !important;\n  top: auto !important;\n  float: none !important;\n  padding-left: initial !important;\n  padding-right: initial !important;\n  vertical-align: inherit !important;\n\n  display: inline-block;\n  margin-left: -72px;\n  width: 60px;\n  margin-right: 12px;\n  height: 1em;\n  line-height: 1em;\n  text-align: right;\n  color: var(--light-brown);\n  opacity: 0.5;\n  font-size: 14px;\n  font-weight: normal;\n}\n#write h1::before {\n  content: 'h1';\n}\n#write h2::before {\n  content: 'h2';\n}\n#write h3::before {\n  content: 'h3';\n}\n#write h4::before {\n  content: 'h4';\n}\n#write h5::before {\n  content: 'h5';\n}\n#write h6::before {\n  content: 'h6';\n}\n\n@media screen and (max-width: 900px) {\n  @for $var from 1 to 7 {\n    #write {\n      h#{$var} {\n        margin-left: 2rem;\n      }\n    }\n  }\n}\n\n#write ul,\n#write ol {\n  padding-left: 2em;\n  font-size: 95%;\n}\n#write ul {\n  list-style-type: disc;\n}\n#write ol {\n  list-style-type: decimal;\n}\n#write > ul,\n#write > ol {\n  padding-left: 0;\n  font-size: inherit;\n}\n#write ul ul,\n#write ul ol,\n#write ol ol,\n#write ol ul {\n  margin-top: 0;\n  margin-bottom: 0;\n}\n\n#write li + li {\n  margin-top: 0.25em;\n}\n#write dl {\n  padding: 0;\n}\n#write dl dt {\n  padding: 0;\n  margin-top: 16px;\n  font-size: 1em;\n  font-style: italic;\n  font-weight: bold;\n}\n#write dl dd {\n  padding: 0 16px;\n  margin-bottom: 16px;\n}\n#write .task-list-item {\n  padding-left: 0;\n}\n#write .task-list-item input[type='checkbox'] {\n  margin: 0.36em 5px;\n  margin-left: -24px;\n}\n#write table {\n  display: block;\n  width: 100%;\n  overflow: auto;\n  border-collapse: collapse;\n}\n#write table th {\n  font-weight: bold;\n}\n#write table th,\n#write table td {\n  padding: 5px 10px;\n  border: 1px solid rgba(128, 128, 128, 0.1);\n  border-collapse: collapse;\n}\n#write table tr {\n  border-top: 1px solid rgba(128, 128, 128, 0.4);\n  border-collapse: collapse;\n}\n#write img {\n  max-width: 100%;\n  box-sizing: content-box;\n}\n#write img[align='right'] {\n  padding-left: 20px;\n}\n#write img[align='left'] {\n  padding-right: 20px;\n}\n#write .emoji {\n  max-width: none;\n  vertical-align: text-top;\n  background-color: transparent;\n}\n#write span.frame {\n  display: block;\n  overflow: hidden;\n}\n#write span.frame > span {\n  display: block;\n  float: left;\n  width: auto;\n  padding: 7px;\n  margin: 13px 0 0;\n  overflow: hidden;\n  border: 1px solid rgba(128, 128, 128, 0.4);\n}\n#write span.frame span img {\n  display: block;\n  float: left;\n}\n#write span.frame span span {\n  display: block;\n  padding: 5px 0 0;\n  clear: both;\n  color: #333;\n}\n#write span.align-center {\n  display: block;\n  overflow: hidden;\n  clear: both;\n}\n#write span.align-center > span {\n  display: block;\n  margin: 13px auto 0;\n  overflow: hidden;\n  text-align: center;\n}\n#write span.align-center span img {\n  margin: 0 auto;\n  text-align: center;\n}\n#write span.align-right {\n  display: block;\n  overflow: hidden;\n  clear: both;\n}\n#write span.align-right > span {\n  display: block;\n  margin: 13px 0 0;\n  overflow: hidden;\n  text-align: right;\n}\n#write span.align-right span img {\n  margin: 0;\n  text-align: right;\n}\n#write span.float-left {\n  display: block;\n  float: left;\n  margin-right: 13px;\n  overflow: hidden;\n}\n#write span.float-left span {\n  margin: 13px 0 0;\n}\n#write span.float-right {\n  display: block;\n  float: right;\n  margin-left: 13px;\n  overflow: hidden;\n}\n#write span.float-right > span {\n  display: block;\n  margin: 13px auto 0;\n  overflow: hidden;\n  text-align: right;\n}\n","@for $var from 1 to 6 {\n  h#{$var} {\n    margin-top: 1rem;\n  }\n}\n.avatar-wrap {\n  position: relative;\n  z-index: 0;\n}\n.avatar-shadow {\n  position: absolute;\n  border-radius: 100%;\n  // width: 200px;\n  top: 0;\n  bottom: 0;\n  right: 0;\n  left: 0;\n  // height: 200px;\n  background: var(--bg) no-repeat center/cover;\n  display: inline-block;\n\n  &::after {\n    position: absolute;\n    left: 0;\n    top: 10%;\n    z-index: -1;\n    border-radius: 100%;\n    width: 100%;\n    height: 100%;\n    background: inherit;\n    filter: blur(10px) brightness(80%) opacity(0.8);\n    content: '';\n    transform: scale(0.95);\n  }\n}\n\n@media screen and (prefers-color-scheme: dark) {\n  :root {\n    --shizuku-background-color: #212121;\n    --shizuku-text-color: #858585;\n    --shizuku-theme-color: #3c946c;\n  }\n  blockquote {\n    background: rgba($color: var(--shizuku-background-color), $alpha: 0.5);\n    border-left: 5px solid var(--shizuku-theme-color);\n  }\n  article {\n    color: var(--shizuku-text-color);\n  }\n}\n\nhtml {\n  font-size: 14px;\n}\n\ninput[type*='date'],\ninput[type='email'],\ninput[type='month'],\ninput[type='number'],\ninput[type='password'],\ninput[type='search'],\ninput[type='tel'],\ninput[type='text'],\ninput[type='time'],\ninput[type='url'],\ninput[type='week'],\nselect,\ntextarea {\n  min-height: unset;\n}\n\n@media (max-width: 600px) {\n  .head-menu a {\n    display: inline-block;\n  }\n}\nimg,\nsvg,\naudio,\nvideo,\niframe {\n  vertical-align: unset;\n}\n\npre,\ncode {\n  font-family: JetBrains Mono, Operator Mono, Monaco, Consolas, 'Courier New',\n    Courier, monospace !important;\n}\n\n.ant-comment-actions {\n  margin-left: 0;\n}\n.ant-pagination-prev .ant-pagination-item-link,\n.ant-pagination-next .ant-pagination-item-link {\n  background: none;\n}\n","@charset \"UTF-8\";\n\n/* ----\n\n# Kico Style 1.0\n# By: Dreamer-Paul\n# Last Update: 2020.3.31\n\n一个可口的极简响应式前端框架。\n\n本代码为奇趣保罗原创，并遵守 MIT 开源协议。欢迎访问我的博客：https://paugram.com\n\n---- */\n\n/* 0 - 全局\n-------------------------------- */\nhtml, body,\ndl, dt, dd, ol, ul,\nh1, h2, h3, h4, h5, h6,\npre, code, form, p,\nfieldset, legend, figure{\n    margin: 0;\n    padding: 0;\n}\n\n*, *:before, *:after{ box-sizing: border-box }\n\narticle, aside, figcaption, figure, footer, header, hgroup, main, nav, section{ display: block }\n\nhtml{\n    color: #353535;\n    scroll-behavior: smooth;\n    font: 16px/1.5 'Microsoft Yahei', 'PingFang SC', 'Hiragino Sans GB', sans-serif;\n}\n\nhtml.font-s{ font-size: 14px }\nhtml.font-m{ font-size: 16px }\nhtml.font-l{ font-size: 18px }\n\n:root{\n    --red: #ea644a;\n    --yellow: #ffb03a;\n    --blue: #3498db;\n    --green: #27a17e;\n}\n\n@media screen and (max-width: 768px){\n    html.font-auto{ font-size: 14px }\n}\n\n@media screen and (min-width: 1921px){\n    html.font-auto{ font-size: 18px }\n}\n\n/* - 选择内容 */\n::-moz-selection{\n    color: #fff;\n    background-color: rgba(0, 0, 0, .66);\n}\n\n::selection{\n    color: #fff;\n    background-color: rgba(0, 0, 0, .66);\n}\n\n/* - 滚动条 */\n::-webkit-scrollbar{\n    width: 10px;\n    height: 10px;\n}\n\n::-webkit-scrollbar-thumb{\n    background: #ccc;\n    border-radius: 5px;\n}\n::-webkit-scrollbar-track{ border-radius: 5px }\n::-webkit-scrollbar-track:hover{ background: rgba(0, 0, 0, .05) }\n\nbody::-webkit-scrollbar-track{ border-radius: 0 }\n\n/* 1 - 容器\n-------------------------------- */\n.wrap{\n    margin: 0 auto;\n    max-width: 1200px;\n    padding: 0 1.25em;\n    box-sizing: content-box;\n}\n\n.wrap.min{ max-width: 800px }\n.wrap.mid{ max-width: 1000px }\n.wrap.max{ max-width: 1400px }\n.wrap.full{ max-width: 100% }\n\n.wrap.thin{ padding: 0 .75em }\n.wrap.thick{ padding: 0 1.5em }\n.wrap.clear{\n    padding-left: 0;\n    padding-right: 0;\n}\n\n.float-none{ float: none !important }\n.float-left{ float: left !important }\n.float-right{ float: right !important }\n\n.clearfix:after{\n    content: '';\n    clear: both;\n    display: block;\n}\n\n/* 2 - 元素\n-------------------------------- */\nh1{ font-size: 2em }\n\nh1, h2, h3, h4, h5, h6{ margin-bottom: 1rem }\n\nh1:last-child, h2:last-child, h3:last-child, h4:last-child, h5:last-child, h6:last-child, p:last-child{ margin-bottom: 0 }\n\np{\n    line-height: 1.8;\n    margin-bottom: 1em;\n}\n\na{\n    color: #3498db;\n    text-decoration: none;\n}\n\na:hover{ color: #ffc670 }\n\nabbr[title]{\n    cursor: help;\n    text-decoration: none;\n    border-bottom: 1px dotted;\n}\n\nem, mark, kbd{\n    font-size: .85em;\n    padding: .25em .5em;\n    border-radius: .5em;\n}\n\nem{\n    color: #fff;\n    font-style: normal;\n    background: #3498db;\n}\nem.red{ background: var(--red) }\nem.yellow{ background: var(--yellow) }\nem.blue{ background: var(--blue) }\nem.green{ background: var(--green) }\n\nkbd{\n    color: #fff;\n    background: #333;\n    font-family: 'Consolas', 'Courier New', monospace, \"微软雅黑\";\n}\n\nimg, svg, audio, video, iframe{\n    max-width: 100%;\n    vertical-align: middle;\n}\n\naudio, video{ outline: none }\n\n/* - 文章 */\narticle{\n    word-break: break-all;\n    letter-spacing: .03em;\n}\n\narticle > *{ margin-bottom: 1em }\narticle > *:last-child{ margin-bottom: 0 }\n\narticle h1, article h2, article h3{ font-size: 1.2em }\narticle h4, article h5, article h6{ font-size: 1.1em }\n\narticle ul, article ol, article dl{ line-height: 1.8 }\n\n/* - 按钮 */\nbutton{\n    outline: 0;\n    font: inherit;\n}\n\n.btn{\n    color: inherit;\n    cursor: pointer;\n    background: #fff;\n    padding: .5em 1em;\n    border-radius: 4px;\n    display: inline-block;\n    border: 1px solid transparent;\n}\n.btn:hover{ color: inherit }\n\n/* -- 禁用的按钮 */\n.btn[disabled]{\n    opacity: .5;\n    cursor: not-allowed;\n}\n\n/* -- 按钮尺寸 */\n.btn.small{ font-size: .85em }\n.btn.middle, .btn.large{ padding: .75em 1.5em }\n.btn.large{ font-size: 1.2em }\n\n/* -- 按钮颜色 */\n.btn.red, .btn.yellow, .btn.blue, .btn.green{ color: #fff }\n\n.btn.red{\n    background: #ea644a;\n    background: var(--red);\n}\n.btn.yellow{\n    background: #ffb03a;\n    background: var(--yellow);\n}\n.btn.blue{\n    background: #3498db;\n    background: var(--blue);\n}\n.btn.green{\n    background: #27a17e;\n    background: var(--green);\n}\n.btn.transparent{ background: transparent }\n\n/* - 代码 */\npre, code{ font-family: 'Consolas', 'Courier New', monospace, \"微软雅黑\" }\n\n:not(pre) > code{\n    color: #c40b00;\n    font-size: 85%;\n    word-wrap: normal;\n    border-radius: .5em;\n    padding: .25em .5em;\n    word-break: break-all;\n    background-color: #f7f2f4;\n}\n\npre > code{\n    color: #fff;\n    tab-size: 4;\n    padding: 1em;\n    display: block;\n    overflow-x: auto;\n    word-break: normal;\n    font-size: inherit;\n    border-radius: 5px;\n    background-color: #333;\n}\n\n/* - 项目列表 */\nul, ol{ margin-left: 1.25em }\nul.clear, ol.clear{\n    margin-left: 0;\n    list-style: none;\n}\n\ndl dd{ margin-left: 1.5em }\ndl dd:before{\n    content: \"--\";\n    margin-right: .25em;\n}\n\n/* - 补间动画 */\na, .btn{\n    transition: color 0.3s, background 0.3s;\n    -o-transition: color 0.3s, background 0.3s;\n    -moz-transition: color 0.3s, background 0.3s;\n    -webkit-transition: color 0.3s, background 0.3s;\n}\n\n/* - 引用*/\nblockquote{\n    margin: 0 0 1em;\n    line-height: 1.8;\n    font-style: oblique;\n    background: #f5fafd;\n    padding: 1em 1em 1em 2em;\n    border-left: 5px #3498db solid;\n}\n\ncite{\n    color: #3498db;\n    font-style: normal;\n}\n\n/* - 分割线 */\nhr{\n    border: 0;\n    margin: 1.5em 0;\n    border-top: 1px rgba(0, 0, 0, 0.5) solid;\n}\nhr.light{\n    border-top: 1px rgba(255, 255, 255, 0.5) solid;\n}\n\n/* - 表单 */\ninput[disabled], textarea[disabled]{\n    cursor: no-drop !important;\n}\n\ninput[disabled], textarea[disabled], input[readonly]{\n    background: rgb(235, 235, 228);\n}\n\ninput, select, textarea{\n    outline: none;\n    font: inherit;\n    max-width: 100%;\n    background: none;\n    vertical-align: middle;\n}\n\ninput[type*=\"date\"], input[type=\"email\"], input[type=\"month\"], input[type=\"number\"], input[type=\"password\"], input[type=\"search\"], input[type=\"tel\"], input[type=\"text\"], input[type=\"time\"], input[type=\"url\"], input[type=\"week\"],\nselect, textarea{\n    padding: .5em;\n    color: inherit;\n    border-radius: 4px;\n    border: #ccc 1px solid;\n    min-height: calc(2.5em + 2px);\n}\n\ninput.invalid, input:out-of-range{\n    border-color: #c40b00;